<template>
  <div>
    <el-upload
      drag
      action="default"
      :before-upload="handleImport"
      class="import-uploader">
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">
        <span>将导出的 data.json 文件拖到此处，或</span>
        <el-button size="small" type="success" icon="el-icon-upload2">点击导入</el-button>
      </div>
    </el-upload>
    <hr />
    <el-button type="danger" icon="el-icon-delete" @click="handleResetLocalDB">重置本地数据库</el-button>
    <el-button type="success" icon="el-icon-download" @click="handleExport">数据导出</el-button>
  </div>
</template>

<script>
import vuex from '@/mixins/vuex.js'
export default {
  mixins: [
    vuex
  ],
  methods: {
    // 重置本地数据
    handleResetLocalDB () {
      this.$confirm('此操作将会把存在浏览器中的数据清空', '重置本地数据库?', {
        confirmButtonText: '我要清空数据',
        cancelButtonText: '点错了',
        type: 'warning'
      }).then(() => {
        this.vuexResetAll()
          .then(() => {
            this.vuexLoadAll()
            this.$message({
              message: '重置本地数据库成功',
              type: 'success'
            })
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        })
      })
    },
    // 导出数据备份
    handleExport () {
      this.$confirm('把本地数据导出成数据文件', '提示：', {
        confirmButtonText: '确认导出',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.vuexExport()
          .then(() => {
            this.$message({
              message: '新建云端备份成功',
              type: 'success'
            })
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        })
      })
    },
    // 获取文件
    handleImport (file) {
      const reader = new FileReader()
      reader.readAsText(file)
      reader.onload = (res) => {
        this.$confirm(`导入数据会覆盖本地数据`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          const result = JSON.parse(res.target.result)
          const state = result.jsonStr
          this.vuexDefault(state)
          this.vuexLoadAll()
          this.$message({
            message: `导入数据成功`,
            type: 'success'
          })
        }).catch((err) => {
          console.log(err)
          this.$message({
            type: 'info',
            message: '已取消导入'
          })
        })
      }
      return false
    }
  }
}
</script>

<style lang="scss">
.import-uploader {
  .el-upload {
    display: block;
    .el-upload-dragger {
      width: auto;
    }
  }
}
</style>



